
*{margin:0;padding: 0;
}
html, body, form, h1, h2, h3, h4, h5, h6, div, dl, dt, dd, ul, ol, li, p, em, address, a, input, textarea, span, strong, img{margin: 0px;padding: 0px
}
body{font-size: 12px;color: #333;font-family:"\5FAE\8F6F\96C5\9ED1";
}
ul,li,ol{list-style: none
}
input, textarea, select, button{font-size: 12px
}
img{border: none
}
a{color: #333;text-decoration: none;outline: none;-webkit-transition: color 0.5s ease;transition: color 0.5s ease;
}
table{border-collapse: collapse
}
table, thead, tbody, tfoot, tr, td{margin: 0px
}
.clear{clear: both;height: 0px;line-height: 0px;font-size: 0px;overflow: hidden;visibility: hidden
}
/*h1, h2, h3, h4, h5, h6{font-size: 12px;font-weight: normal}*/
h2{margin: 20px 0 10px 0;
}
h4{color: #666;
}
i,em{font-style: normal;
}
::-moz-selection{background: #6f346a;color: #fff
}
::selection{background: #6f346a;color: #fff
}
::-moz-selection{background:#6f346a;
color:#fff
}
::-webkit-selection{background:#6f346a;
color:#fff
}
section, article, aside, header, footer, nav, dialog, figure{display: block;
}
.clearfix:after{content:".";display:block;height:0;clear:both;visibility:hidden;
}
.clearfix{*+height:1%;
}
a{text-decoration: none;font-size:14px;color:#000;
}
li{list-style: none;
}
.fl{float: left;
}
.fr{float: right;
}
.po_rel{position: relative;
}
.po_abs{position: absolute;
}
.pl_40{padding-left: 40px;
}
.pr_40{padding-right: 40px;
}
#app {

  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}

.lab1{
    font-size: 18px;
    text-align: center;
    line-height: 30px;
    margin-top: 20px;
}
.dselect{
    max-width: 600px;
    margin: 0 auto;
}
.dselect p, .dselect div{
    cursor: pointer;
}
.personnel_left,.personnel_right{
    float: left;
    width: 48%;
    border: 1px solid #DEDEDE;
    border-radius: 4px;
    background: #F4F6F8;
    height: 400px;
    margin-top: 50px;
    overflow-y: auto;
}
.personnel_right{
    float:right;
}
.allperson > p{
    font-size: 16px;
}
.department_list{
    padding-left: 20px;
}
.dising_group{
    padding: 20px 40px;
}
.dising_group p{font-size: 16px;line-height: 40px;
}
.dising_group p span,.dising_group p label{
    cursor: pointer;
}
.dising_group img{width:30px;height:40px;display:inline-block;vertical-align: middle;
}
.dising_group div{font-size: 14px;padding-left: 20px;
}
.listsitms{
    padding:10px 5px;
    vertical-align: middle;
}
.listsitms:hover{
    background:#E7EAEF;
}
.listsitms span img{
    display: inline-block;
    width: 30px;
    height: 30px;
    border-radius: 50%;
}
.listsitms input[type="checkbox"]{
    width: 15px;
    height: 15px;
    cursor: pointer;
}
